.complete_container {
  width: 100%;
  height: 50px;

  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 20px;
}

.complete_cancel {
  width: 40%;
  height: 80%;

  opacity: 0.6;
}

.complete_save {
  width: 40%;
  height: 80%;
  opacity: 0.6;
}

.complete_save:hover {
  opacity: 1;
}

.complete_cancel:hover {
  opacity: 1;
}
